@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Extrabold.woff") format("woff"), url("../fonts/Gilroy-Extrabold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.woff") format("woff"), url("../fonts/Gilroy-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Light.woff") format("woff"), url("../fonts/Gilroy-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Light.woff") format("woff"), url("../fonts/Gilroy-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Bold.woff") format("woff"), url("../fonts/SFProDisplay-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Light.woff") format("woff"), url("../fonts/SFProDisplay-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Medium.woff") format("woff"), url("../fonts/SFProDisplay-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Regular.woff") format("woff"), url("../fonts/SFProDisplay-Medium.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-family: "SFProDisplay";
  background-color: #f9efe7;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  font-style: normal;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.container {
  min-width: 320px;
  max-width: 1330px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

.header {
  padding-top: 60px;
}

.header__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header__info-item {
  display: flex;
}

.header__info-item:not(:last-child) {
  margin-bottom: 15px;
}

.header__info-item.header__info-item--decoration {
  padding-bottom: 15px;
  border-bottom: 1px solid #dbbbb5;
}

.header__info-link {
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
  color: #000;
  transition: color .2s linear;
}

.header__info-link span {
  color: #88480F;
  transition: color .2s linear;
}

.header__info-link.header__info-link--icon {
  padding-left: 27px;
  position: relative;
}

.header__info-link.header__info-link--icon::before {
  content: '';
  width: 14px;
  height: 14px;
  position: absolute;
  left: 0;
  top: 2px;
  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
}

.header__info-link:not(:last-child) {
  margin-right: 17px;
}

.header__info-link:hover {
  color: #0092b5;
}

.header__info-link:hover span {
  color: #0092b5;
}

.header__info-top-location {
  padding-left: 20px;
  position: relative;
  margin-left: 25px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  color: #88480F;
}

.header__info-top-location:before {
  content: '';
  width: 9px;
  height: 13px;
  position: absolute;
  left: 0;
  top: 3px;
  background-image: url("../img/location.svg");
  background-repeat: no-repeat;
}

@media (max-width: 1167px) {
  .header__info-item {
    flex-direction: column;
  }
  .header__info-top-location {
    margin-left: 0;
  }
  .header__info-links {
    margin-bottom: 10px;
  }
}

@media (max-width: 849px) {
  .header__info {
    display: block;
  }
  .header__info-logo {
    margin-bottom: 20px;
  }
}

@media (max-width: 1126px) {
  .nav__list-link {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

.nav {
  padding: 5px;
  margin-top: 40px;
  background: #FFFFFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.17);
  border-radius: 50px;
}

.nav__list {
  display: grid;
  grid-template-columns: repeat(9, max-content);
  grid-column-gap: 15px;
  align-content: center;
  justify-content: space-between;
  padding-left: 47px;
  padding-right: 47px;
  border: 1px solid #FAE5DA;
  border-radius: 50px;
}

.nav__list-link {
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 1;
  color: #000000;
  transition: color .2s linear;
}

.nav__list-link:hover {
  color: #0092b5;
}

@media (max-width: 1163px) {
  .nav__list {
    grid-template-columns: repeat(4, max-content);
  }
  .nav__list-link {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}
